.displayClassesSelectorsContainer {
    padding: 1.5vw 1vw;
    border: 2px solid #CCAE27 ;
    -webkit-animation: slide-in-left 0.7s ease-out both;
                animation: slide-in-left 0.7s ease-out both;
}

.noScoreMessage {
  text-decoration: underline;
  color: red;
}

.yesScoreMessage {
  text-decoration: underline;
  color: rgb(0, 255, 76);
}

.formClasses {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.8vw;
    row-gap: 0;
    margin: 2vh 0;
}
.classContainer {
    display: flex;
    flex-direction: column;
    max-width: 6vw;
    transition: 0.2s ease-in;
    text-align: center;
}
.formClasses p{
    max-width: 6vw;
    max-height: 6vw;
    display: inline-block;
    align-self: center;
    font-size: 0.65rem;
    
}

.classSelectors {
    display: none;
}

.classesLabels {
    display: inline-block;
    justify-content: center;
    box-sizing: border-box;
    width: 6vw;
    height: 6vw;
}

.classesLabels img {
    width: 6vw;
    height: 6vw;
}

.displayClassesDescriptionContainer {
    border: 2px solid #CCAE27 ;
    -webkit-animation: slide-in-right 0.7s ease-in both;
                animation: slide-in-right 0.7s ease-in both;
}

.displayClassesDescriptionContainer h5 {
  border-bottom: 2px solid #CCAE27;
  font-size: 1.2rem;
  line-height: 2rem;
  margin-bottom: 1vh;
  text-shadow: 0 3px 1px rgba(0,0,0);
} 

.displayClassesDescriptionContainer .classLoreContainer {
    max-height: 57vh;
    min-height: 36.5vh;
    max-width: 22vw;
    overflow: overlay;
    margin: 2vh 0.6vw;
    padding: 1vh 1vw;
    border: 1px solid #CCAE27;
    font-size: 0.8rem;
    font-style: italic;
}

.displayClassesDescriptionContainer .classLoreContainer::-webkit-scrollbar {
  display: none;
}

.displayClassesDescriptionContainer .classLoreContainer:hover::-webkit-scrollbar {
  display: initial;
 }

 .displayClassesDescriptionContainer .classLoreContainer::-webkit-scrollbar-thumb {
 background-color: #976820;
}

.displayClassesDescriptionContainer p:nth-child(3){
  margin: 1vh 0px;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.displayClassesDescriptionContainer .noClassContent {
  font-style: italic;
      line-height: 1.7rem;
      font-size: 1.35rem;
      text-shadow: 0 1px 1px #0000005c;
}

.displayClassesDescriptionContainer .classSkillsList li {
  list-style: none;
  text-shadow: 0 3px 1px rgba(0,0,0);
  min-width: 23vw;
  padding: 1vh;
  margin: 0.5vh;
  border: 1px solid #CCAE27;
  background: rgba(0,0,0, 0.9);
  transition: 0.3s ease-in-out;
}

.displayClassesDescriptionContainer .classSkillsList li:hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}

.displayClassesSelectorsContainer table {
  border: 1px solid #CCAE27;
  padding: 0 0.5vw 0.5vh 0;
  font-size: 0.6rem;
  margin: 1vh 0;
  cursor: pointer;
  user-select: none;
  transition: 0.3s ease-in-out;
}

.displayClassesSelectorsContainer table:active {
  position: relative;
  left: 10%;
  transform: scale(1.3);
  background: rgba(0,0,0, 0.9);
  transition: 0.3s ease-in-out;
}

.displayClassesSelectorsContainer .modifiersTable th {
  font-size: 0.7rem;
  line-height: 1.4rem;
  text-shadow: 0 3px 1px rgba(0,0,0);
}

.displayClassesSelectorsContainer .modifiersTable td {
  padding: 0 2vw;
}

.displayClassesSelectorsContainer .divider, .displayClassesDescriptionContainer .divider {
  margin: 2vh 0 2vh 0
}

