.branding {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: 'Press Start 2P', cursive;
    background-color: black;
         -webkit-animation: fade-in-out 4s cubic-bezier(0.390, 0.575, 0.565, 1.000) 3s 2 alternate both;
                animation: fade-in-out 4s cubic-bezier(0.390, 0.575, 0.565, 1.000) 3s 2 alternate both; 
}

.branding h3 {
    font-size: 2rem;
    border-bottom: 10px solid;
    border-image-slice: 1;
    border-width: 5px;
    border-image-source: linear-gradient(to left, #6413b0, #d60042);
    padding-bottom: 10px;
}

.branding h3 span {
    background-color: #6413b0;
    background-image: linear-gradient(90deg, #6413b0, #d60042);
    background-size: 100%;
    -webkit-background-clip: text;
    -moz-background-clip: text;
    -webkit-text-fill-color: transparent; 
    -moz-text-fill-color: transparent;
}

