.startScreen {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    height: 100vh;
    background-color: black;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.startScreen .imageContainer {
    grid-column: 1/1;
    grid-row: 1/span1;
    display: flex;
    justify-content: flex-end;

}

.startMessage {
    grid-column: 2/span1;
    grid-row: 1/-1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5vh;
    max-width: 33vw;
    text-align: center;
    text-indent: 1vw;
    font: 1rem Arial, 'Courier New', Courier, monospace;
}

.startMessage p:nth-child(2) {
    text-align: justify;
}

.startScreen input {
    -webkit-animation: pulsate-fwd 1.3s ease-in-out infinite both;
	        animation: pulsate-fwd 1.3s ease-in-out infinite both;
}

