/*TODO: Combine styles with other components of the DisplayScreen once added to them*/

.characterProfile {
    display: grid;
    grid-template-columns: 2vw 29vw 38vw 29vw 2vw;
    grid-template-rows: 9vh 89vh 2vh;
    background-size: cover;
    background-color: rgba(0,0,0,0.3);
    background-blend-mode: overlay;
    font-family: 'Eagle Lake', cursive;
    font-size: 1rem;
}

.characterProfile h2 {
    grid-column: 5 / 2;
    grid-row: 1 / 1;
    justify-self: center;
    align-self: flex-end;
    font-size: 2rem;
    border-bottom: 2px solid #CCAE27;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.characterProfile .displayImgContainer {
  grid-column: 3 / span 1;
  grid-row: 2 / span 1;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 0 2vh 0;
  max-width: 40vw;
  max-height: 85vh;
} 

.characterProfile .displayImgContainer img {
  max-width: 40vw;
  max-height: 83vh;
}


.characterProfile .characterGeneralInfo {
    grid-column: 2 /span 1;
    grid-row: 2/span 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 1.5vw 1vw;
    border: 2px solid #CCAE27 ;
    background: rgb(0, 0, 0, 0.5);
    -webkit-animation: slide-in-left 0.7s ease-out both;
            animation: slide-in-left 0.7s ease-out both;
}

.characterProfile table {
    border: 1px solid #CCAE27;
    padding: 0 1vw 1vh 0;
    font-size: 0.8rem;
    margin: 1vh 0;
    cursor: pointer;
    user-select: none;
    transition: 0.3s ease-in-out;
  }
  
  .characterProfile table:active {
    position: relative;
    left: 10%;
    transform: scale(1.3);
    background: rgba(0,0,0, 0.9);
    transition: 0.3s ease-in-out;
  }
  
  .characterProfile .modifiersTable th {
    font-size: 0.9rem;
    text-shadow: 0 3px 1px rgba(0,0,0);
  }
  
  .characterProfile .modifiersTable td {
    padding: 0 2vw;
  }

.characterProfile .characterSkillsInfo {
    grid-column: 4 / span 1;
    grid-row: 2 / span 1;

    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(10, 1fr);
    align-items: center;
    justify-items: center;
    border: 2px solid #CCAE27 ;
    background: rgb(0, 0, 0, 0.6);
    -webkit-animation: slide-in-right 0.7s ease-in both;
                animation: slide-in-right 0.7s ease-in both;

    overflow: auto;
}

.characterSkillsInfo img {
    display: inline;
    height: 10vh;
}

.characterSkillsInfo h4 {
    grid-column: 2/span 3;
    justify-self: baseline;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.characterSkillsInfo .characterLore {
    grid-column: 1/-1;
    grid-row: 2/span 5;
    max-height: 40vh;
    overflow: overlay;
    margin: 2vh 0.6vw;
    padding: 1vh 1vw;
    border: 1px solid #CCAE27;
} 

.characterSkillsInfo .characterLore::-webkit-scrollbar {
  display: none;
}

.characterSkillsInfo .characterLore:hover::-webkit-scrollbar {
  display: initial;
 }

 .characterSkillsInfo .characterLore::-webkit-scrollbar-thumb {
 background-color: #976820;
}

.characterProfile h5 {
    grid-column: 1/-1;
    grid-row: 7/span 1;
    margin: 1vh 0px;
    padding-left: 2vw;
    font-size: 1rem;
    justify-self: baseline;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.characterProfile .characterSkillsInfo p:nth-child(2) {
    margin: 1vh 0px;
    padding-left: 2vw;
    align-self: baseline;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.characterProfile .characterSkillsInfo .divider {
  border-bottom: 1px solid #CCAE27;
  width: 90%;
  margin-bottom: 2vh;
}

.characterProfile .razeSkills li, .characterProfile .classSkills li {
  list-style: none;
  text-shadow: 0 3px 1px rgba(0,0,0);
  min-width: 23vw;
  padding: 1vh;
  margin: 0.5vh;
  border: 1px solid #CCAE27;
  background: rgba(0,0,0, 0.9);
  transition: 0.3s ease-in-out;
}

.characterProfile .razeSkills li:hover, .characterProfile .classSkills li:hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}

.characterProfile .continueButton {
  position: absolute;
  left: 39%;
  bottom: 2%;
  width: 25vw;
}