.diceRolls {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 5vw;
    align-items: center;
    justify-items: center;
    background-image: url("https://i.imgur.com/svtJbZs.jpg");
    background-size: cover;
    min-width: 100vw;
    min-height: 100vh;
    font-family: "Eagle Lake", cursive;
    text-shadow: 0 2px 1px rgba(0,0,0);
    -webkit-animation: fade-in 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
            animation: fade-in 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.diceRollTableContainer {
    grid-column: 2/span 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 28vw;
    height: 80vh;
    padding: 3vh 2vw;
    background: rgb(0, 0, 0, 0.8);
    border: 1px solid #d9952e;
    border-radius: 2%;
}

#reRollMessage {
    width: 25.25vw;
    margin: 1vh 0; 
}

.afterRollMessage {
    font-size: 0.8rem;
    text-align: center;
}

.beforeRollMessage {
    font-size: 0.9rem;
    text-align: center;
}

.diceRolls h4 {
    font-size: 1.1rem;
    margin: 1vh 0;
}

.diceRolls h5 {
    font-size: 1rem;
    margin: 3vh 0 0 0;
}

.diceRolls h6 {
    border-bottom: 2px solid #d9952e;
    max-width: fit-content;
    text-shadow: 0 2px 1px rgba(0,0,0);
}

.statsTable {
    min-width: 20vw;
    text-align: center;
    font-size: 1rem;
}

tr {
    width: 5vw;
}

tr th {
    font-size: 1.1rem;
    line-height: 6vh;
}

.diceRollTableContainer .reRollButtons {
    width: 5vw;
    height: 4vh;
    margin-top: 0.7vh;
    font-size: 0.75rem;
}

.screenButtonsContainer {
    display: flex;
    flex-direction: row;
    gap: 0.5vw;
    margin-top: 2vh;
}

.diceRollTableContainer input:hover {
    transform: translate(0, -5px) scale(1.1)
}

.statsDescription {
    grid-column: 4/span 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 28vw;
    max-height: 76vh;
    padding: 3vh 2vw;
    background: rgb(0, 0, 0, 0.8);
    border: 1px solid #d9952e;
    border-radius: 2%;
    overflow: overlay;
}

.statsDescription::-webkit-scrollbar {
    display: none;
  }
  
.statsDescription:hover::-webkit-scrollbar {
    display: initial;
   }
  
.statsDescription::-webkit-scrollbar-thumb {
   background-color: #976820;
  }



.statsDescription p {
    font-size: 1rem;
    text-indent: 2vw;
}

.statsDescription ul {
    padding: 0 1vw 1vw 1vw;
}

.statsDescription li {
    margin-top: 4vh;
    font-size: 1rem;
    list-style: none;
}

.statsDescription h6 {
    font-size: 1rem;
    margin-bottom: 1vh;
}

.pulsate-fwd{
	-webkit-animation: pulsate-fwd 1s ease-in-out infinite both;
	        animation: pulsate-fwd 1s ease-in-out infinite both;
}

