.mainMenu {
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    -webkit-animation: fade-in 6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
            animation: fade-in 6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.mainMenuDiv {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.mainMenu img {
  position: relative;
  right: 2%;
}

.mainMenuSubtitle {
    font-family: 'Eagle Lake', cursive;
    font-size: 4rem;
    margin: -40px 0px 0px 0px;
    background: #FCEABB;
    background: radial-gradient(ellipse farthest-corner at top center, #FCEABB 0%, #FCCD4D 50%, #F8B500 51%, #FBDF93 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.mainMenuForm {
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 25px;
}

.mainMenu input {
  font-size: 1.3rem;
}

.mainMenu p {
    position: absolute;
    bottom: 2vh;
    font-family: Arial, Helvetica, sans-serif;
}

