.displayScreen {
    display: grid;
    grid-template-columns: 2vw 29vw 38vw 29vw 2vw;
    grid-template-rows: 9vh 89vh 2vh;
    background-size: cover;
    background-color: rgba(0,0,0,0.2);
    background-blend-mode: overlay;
    font-family: 'Eagle Lake', cursive;
    font-size: 1rem;
    -webkit-animation: fade-in 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
            animation: fade-in 0.6s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.displayScreen h2 {
    grid-column: 5 / 2;
    grid-row: 1 / 1;
    justify-self: center;
    align-self: flex-end;
    font-size: 2rem;
    border-bottom: 2px solid #CCAE27;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.displayImgContainer {
    grid-column: 3 / span 1;
    grid-row: 2 / span 1;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 0 2vh 0;
    max-width: 40vw;
    max-height: 85vh;
}

.displayImgContainer img {
    max-width: 40vw;
    max-height: 85vh;
}

.displayRazesSelectorsContainer, .displayClassesSelectorsContainer {
    grid-column: 2 /span 1;
    grid-row: 2/span 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5vw 1vw;
    background-color: rgba(0,0,0, 0.7);
    border: 2px solid #CCAE27 ;
    border-radius: 1%;
    -webkit-animation: slide-in-left 0.7s ease-out both;
	        animation: slide-in-left 0.7s ease-out both;
}

.displayRazesDescriptionContainer, .displayClassesDescriptionContainer {
    grid-column: 4 / span 1;
    grid-row: 2 / span 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5vh 1vw;
    background-color: rgba(0,0,0, 0.4);
    border: 2px solid #CCAE27 ;
    border-radius: 1%;
    -webkit-animation: slide-in-right 0.7s ease-out both;
                animation: slide-in-right 0.7s ease-out both;
}

.displayScreen .continueButton {
    position: absolute;
    left: 39%;
    bottom: 2%;
    width: 25vw;
  }

.returnButton {
    position: absolute;
    top: 2%;
    left: 2%;
}