.displayRazesSelectorsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0.5vw 1vw;
    background-color: rgba(0,0,0, 0.7);
    border: 2px solid #CCAE27 ;
    border-radius: 1%;
    -webkit-animation: slide-in-left 0.7s ease-out both;
	        animation: slide-in-left 0.7s ease-out both;
}

.displayRazesSelectorsContainer legend {
  display: inline;
    align-self: flex-start;
    margin: 1vh 0px;
    padding-left: 0.7vw;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

input[type="text"] {
    min-height: 5vh;
    min-width: 90%;
    padding: 0 1vw;
    background-image: url("../../img/papyrus.png") ;
    /* background-color: rgba(0,0,0,0.1); */
    background-size: cover;
    /* background-blend-mode: overlay; */
    box-shadow: inset 0px 0px 7px 3px #141414;
    border-color: repeating-radial-gradient(ellipse farthest-side at center center, #B37A5F 0%, #C19A6C 100%);
    border: 1px solid #B37A5F;
    border-radius: 1vh;
    font-family: 'MedievalSharp', cursive;
    font-size: 1.2rem;
    font-weight:600;
    font-style: italic;
    line-height: 4vh;
    color: rgb(49, 27, 6);
}

input[type="text"]::placeholder {
    text-shadow: none;
    color: rgba(0, 0, 0, 0.685);
}

.genderButtons {
  display: flex;
  flex-direction: row;
  gap: 2vw;
  margin: 0 0 1vh 0;
}

.genderLabels {
  display: inline-block;
  width: 3vw;
  height: 3vw;
  transform: scale(1.1);
  transition: 0.3s ease-out;
  cursor: pointer;
}

.genderLabels:hover {
  transform: scale(1.4);
  transition: 0.3s ease-out;
}

.genderRadioSelectors {
  display: none;
}

#maleLabel {
  background-image: url("../../img/male.png");
  background-size: cover;
}
#femaleLabel {
  background-image: url("../../img/female.png");
  background-size: cover;
}

.displayRazesSelectorsContainer table {
  border: 1px solid #CCAE27;
  padding: 0 1vw 1vh 0;
  font-size: 0.8rem;
  margin: 1vh 0;
  cursor: pointer;
  user-select: none;
  transition: 0.3s ease-in-out;
}

.displayRazesSelectorsContainer table:active {
  position: relative;
  left: 10%;
  transform: scale(1.3);
  background: rgba(0,0,0, 0.9);
  transition: 0.3s ease-in-out;
}

.displayRazesSelectorsContainer .modifiersTable th {
  font-size: 0.9rem;
  text-shadow: 0 3px 1px rgba(0,0,0);
}

.displayRazesSelectorsContainer .modifiersTable td {
  padding: 0 2vw;
}

.displayRazesSelectorsContainer p:nth-child(6) {
  padding: 0.5vh;
    border-bottom: 1px solid #CCAE27;
}

.formRazes {
    display: flex;
    flex-direction: row;
    gap: 1vw;
    margin: 2vh 0;
}
.razeContainer {
    display: flex;
    flex-direction: column;
    max-width: 6vw;
    transition: 0.2s ease-in;
}

.formRazes p{
    max-width: 6vw;
    max-height: 6vw;
    display: inline-block;
    align-self: center;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.razesLabels {
    display: inline-block;
    justify-content: center;
    box-sizing: border-box;
    width: 6vw;
    height: 6vw;
    cursor: pointer;
}

.razesLabels img {
    width: 6vw;
    height: 6vw;
    border: 1px solid #CCAE27;
    border-radius: 7%;
    box-shadow: 7px 7px 3px #141414;
}

.razeContainer:hover {
    transform: scale(1.2);
}

.displayRazesDescriptionContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
    padding: 1.5vw 1vw;
    background-color: rgba(0,0,0, 0.4);
    border: 2px solid #CCAE27 ;
    border-radius: 1%;
    -webkit-animation: slide-in-right 0.7s ease-out both;
                animation: slide-in-right 0.7s ease-out both;
}

.displayRazesDescriptionContainer .noRazeContent {
      font-style: italic;
      line-height: 1.7rem;
      font-size: 1.2rem;
      text-shadow: 0 1px 1px #0000005c;
}

.displayRazesDescriptionContainer .razeLoreContainer {
    max-height: 46vh;
    min-height: 46vh;
    overflow: overlay;
    margin: 2vh 0.6vw 0 0.6vw;
    padding: 1vh 1vw;
    border: 1px solid #CCAE27;
    font-size: 0.8rem;
    font-style: italic;
}

.displayRazesDescriptionContainer .razeLoreContainer::-webkit-scrollbar {
  display: none;
}

.displayRazesDescriptionContainer .razeLoreContainer:hover::-webkit-scrollbar {
  display: initial;
 }

 .displayRazesDescriptionContainer .razeLoreContainer::-webkit-scrollbar-thumb {
 background-color: #976820;
}

.displayRazesDescriptionContainer p:nth-child(2) {
    margin: 1vh 0px;
    padding-left: 2vw;
    align-self: baseline;
    text-shadow: 0 3px 1px rgba(0,0,0);
}

.displayRazesDescriptionContainer .divider, .displayRazesSelectorsContainer .divider {
  border-bottom: 1px solid #CCAE27;
  width: 90%;
  margin: 1vh 0;
}

.displayRazesDescriptionContainer .razeSkillsList li {
  list-style: none;
  text-shadow: 0 3px 1px rgba(0,0,0);
  min-width: 23vw;
  padding: 1vh;
  margin: 0.5vh;
  border: 1px solid #CCAE27;
  background: rgba(0,0,0, 0.9);
  transition: 0.3s ease-in-out;
}

.displayRazesDescriptionContainer .razeSkillsList li:hover {
  transform: scale(1.1);
  transition: 0.3s ease-in-out;
}

